import React, { useState } from 'react'
import { Modal, Select, Form, Input, message } from "antd";
import Axios from "axios"

export default function ContactUs(props: any) {
    const { isModalVisible, setIsModalVisible } = props
    const [Type, setType] = useState("feedback");
    const [Content, setContent] = useState("");
    const [Email, setEmail] = useState("");
    const [PhoneNumber, setPhoneNumber] = useState("");

    const { Option } = Select;
    const { TextArea } = Input;

    function handleType(value: string) {
        setType(value)
    }

    const handleContent = (event: any) => {
        setContent(event.currentTarget.value);
    }

    const handleEmail = (event: any) => {
        setEmail(event.currentTarget.value);
    }

    const handlePhoneNumber = (event: any) => {
        setPhoneNumber(event.currentTarget.value);
    }

    const handleOk = () => {
        const feedback = {
            Type: Type,
            Email: Email,
            PhoneNumber: PhoneNumber,
            Content: Content
        }
        if (feedback.Content.length) {
            Axios.post("api/users/send-feedback", feedback).then((response) => {
                if (response.data.success) {
                    message.success('. .');
                } else {
                    message.error(' .  .');
                }
                setType("feedback")
                setContent("")
                setEmail("")
                setPhoneNumber("")
            })
            setIsModalVisible(false);
        } else {
            message.error(" .");
        }

    };

    const handleCancel = () => {
        setIsModalVisible(false);
    };

    return (
        <div>
            <Modal
                title=""
                visible={isModalVisible}
                onOk={handleOk}
                onCancel={handleCancel}
            >
                <p>     . ,    .</p>
                <p />
                <div>
                    <Select defaultValue="feedback" style={{ width: 120 }} onChange={handleType}>
                        <Option value="feedback"></Option>
                        <Option value="ask"></Option>
                        <Option value="copywrite"> </Option>
                    </Select>
                </div>
                <Form.Item required>
                    <p />
                    <TextArea placeholder=" " rows={6} value={Content} onChange={handleContent} />
                    <p />
                     :
                <Input placeholder=" " value={Email} onChange={handleEmail} />
                    <p />
                     :
                <Input placeholder=" " value={PhoneNumber} onChange={handlePhoneNumber} />
                </Form.Item>
            </Modal>
        </div>
    )
}
